"use strict";
var _=require('lodash');
var Promise=require('bluebird');
var moment=require('moment');
var $ = require('../jquery');
var Backgrid=require('../backgrid');
var Backbone= require('../backbone');
var Obiwang = require('../models');
var validator=require('../validator.js');
var util=require('../util');
var BackgridCells=require('../backgrid.cell.js');
var Backform=require('../backform');
var JST=require('../JST');
var main=require('./data');
var CommentModalView=require('./comment');

var Accounting=main.baseDataView.extend({
    collectionName:'TimeRangeGeneral',
    collectionUrl:'/Accounting/',
	title:'Accounting',
	paginator:true,
	//filterFields:['clientName','contractCategory','lead','leadName','status','major','country','degree'],
	renderOptions:{date:true},
    ready:true,
    constructColumns:function(){
    	var self=this;
        var matchCell=Backgrid.Cell.extend({
            template: _.template("<a>Match</a>"),
            events: {
              "click a": "match"
            },
            match: function (e) {
              e.preventDefault();
              var toupdate={};
              toupdate.receivedNontaxable=this.model.get('nontaxable');
              toupdate.receivedRemittances=this.model.get('remittances');
              toupdate.receivedOther=this.model.get('other');
              toupdate.receivedTotal=this.model.get('totalpay');
              this.model.save(toupdate,{save:false});
            },
            render: function () {
              this.$el.html(this.template());
              this.delegateEvents();
              return this;
            }
        });
        this.columns=[
        {name:'createdAt',label:'',editable:false,cell:'date'},
        {name:'servicepay',label:'',editable:false,cell:'number'},
        {name:'nontaxable',label:'',editable:false,cell:'number'},
        {name:'remittances',label:'',editable:false,cell:'number'},
        {name:'other',label:'',editable:false,cell:'number'},
        {name:'totalpay',label:'',editable: false,cell:'number'},
        {name:'account',label:'',editable: false,cell:'string'},
        {name:'',label:'Match',cell:matchCell},
        {name:'receiveDate',label:'',cell:'date'},
        {name:'receivedServicepay',label:'',editable:false,cell:'number'},
        {name:'receivedNontaxable',label:'',cell:'number'},
        {name:'receivedRemittances',label:'',cell:'number'},
        {name:'receivedOther',label:'',cell:'number'},
        {name:'receivedTotal',label:'',cell:'number'}
        ];
        return Promise.resolve({});
        
	},   
});

module.exports=Accounting;
