var sales=main.baseDataView.extend({
    collectionName:'MonthlyCollection',
    collectionUrl:'/SalesComission/',
    title:'',
    paginator:true,
    renderOptions:{month:true},
    templateName:'default',
    constructColumns:function(){
        var self=this;
        return util.ajaxGET('/salesGroup/').then(function(data){
            var myselect=BackgridCells.SelectCell({name:'',values:_.map(data,function(e){return [e.salesGroup,e.id]})});
            self.columns=[
            {name:'chineseName',label:'',editable:false,cell:'string'},
            {name:'nickname',label:'',editable: false,cell:'string'},
            {name:'UserCount',label:'',editable:false,cell:'integer'},
            {name:'contractSigned',label:'',editable:false,cell:'date'},
            {name:'contractPrice',label:'',editable:false,cell:'number'},
            {name:'altPrice',label:'',editable:false,cell:'number'},
            {name:'amountBeforeGoal',label:'',editable:false,cell:'number'},
            {name:'goal',label:'',editable:false,cell:'number'},
            {name:'amountAfterGoal',label:'',editable: false,cell:'number'},
            {name:'role',label:'/',editable:false,cell:'string'},
            {name:'salesGroup',label:'',editable:false,cell:myselect},
            {name:'percent',label:'',editable: false,cell:Backgrid.NumberCell.extend({decimals:3})},
            {name:'comission',label:'',editable:false,cell:'number'},
            ];

            return Promise.resolve({});
        }); 
    },
    destroy: function () {
        this.$el.removeClass('active');
        this.undelegateEvents();
    },
    afterRender:function(){
        this.$el.attr('id', this.id);
        this.$el.addClass('active');
    },   
});

